@use "../abstracts/variables";
@use "../abstracts/mixins";

.headings-home {
  margin-top: -5px;
  font-size: variables.$font-size--mobile--h1-customer;
  line-height: variables.$line-height--mobile--h1-customer;

  @include mixins.media("tablet-l") {
    margin-top: -3px;
    line-height: 1em;
    margin-bottom: 40px;
    font-size: variables.$font-size--mobile-h1;
  }

  &.instagram__account-name {
    margin-bottom: 5px;
  }
}

.sellers-buyers.section-space {
  @include mixins.media("tablet-l") {
    margin-bottom: 28px !important;
  }

  h1 {
    @include mixins.media("tablet-l") {
      margin-top: -5px;
      margin-bottom: 40px !important;
    }
  }
}

.slider-section,
.reviews,
.collage-custom,
.text-section {
  &.section-space {
    @include mixins.media("tablet-l") {
      margin-bottom: 40px;
    }
  }
}

.hero.section-space--hero {
  @include mixins.media("tablet-l") {
    margin-bottom: 40px;
  }
}

h1,
.h2 {
  color: variables.$color-text;
}

.instagram__account-name {
  color: variables.$color-black !important;
}
