@use "../abstracts/variables";
@use "../abstracts/mixins";

.marquee {
  overflow: hidden;
  white-space: nowrap;
  padding: 35px 0;
  font-weight: 600;
  font-size: 18px;
  font-family: variables.$font-family--title;

  color: var(--marque-color);
  background-color: var(--marque-background);

  &:hover .marquee_scroll {
    @include mixins.media('tablet-l') {
      animation-play-state: paused;
    }
  }

  &.touch .marquee_scroll {
    @include mixins.mediaCustom('max-width: 991px') {
      animation-play-state: paused;
    }
  }

  a {
    color: inherit;
  }

  &_scroll {
    animation: marquee 12s linear infinite;
    animation-play-state: running;
    animation-duration: var(--marque-time);
    display: inline-block;

    &--break {
      padding: 0 30px;
    }
  }

  @keyframes marquee {
    0% {
      transform: translateX(0);
    }
    100% {
      transform: translateX(-100%);
    }
  }
}
