@use '../abstracts/variables';
@use '../abstracts/mixins';

.instagram {
  margin-bottom: 10px;

  .loading {
    display: block;
    height: 30px;
    margin: 80px auto 40px;
    width: 30px;
  }
}

.instagram__header {
  margin-bottom: 12px;

  @include mixins.media('tablet-l') {
    margin-bottom: 40px;
  }
}

.instagram__account-name,
.instagram__hashtag,
.instagram__header {
  text-align: center;
}

.instagram__account-name,
.instagram__hashtag {
  text-transform: uppercase;
}

.instagram__account-name {
  margin-bottom: 5px;
}

.instagram__hashtag {
  color: variables.$color-principal;
}

.instagram__posts {
  line-height: 0;
  @include mixins.boxGrid(repeat(3, 1fr), unset, 8px, unset);

  @include mixins.media('tablet') {
    @include mixins.boxGrid(repeat(6, 1fr), unset, 13px, unset);
  }
}

.instagram__post {
  position: relative;
  padding-bottom: 100%;
  overflow: hidden;
  @include mixins.clickEffect;

  &:hover .instagram__post-image {
    transform: scale(1.1);
  }

  &:nth-of-type(4),
  &:nth-of-type(5),
  &:nth-of-type(6) {
    @include mixins.mediaCustom('max-width: 749px') {
      display: none;
    }
  }
}

.instagram__post-image {
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform .3s;
}

.instagram__post-popup {
  top: -100%;
  position: fixed;
  z-index: 20;
  left: 50%;
  width: 90%;
  max-width: 362px;
  transform: translate(-50%, -100%);
  background-color: variables.$color-white;
  color: variables.$color-background-2;
  font-weight: 400;
  transition: all .4s ease-in;

  @include mixins.media('tablet-l') {
    max-width: 999px;
  }

  &[data-active="true"] {
    top: 50%;
    transform: translate(-50%, -50%);
  }

  &[data-active="false"] {
    top: -100%;
    transform: translate(-50%, -100%);
  }

  #loading, .loading  {
    margin: 70% auto;

    @include mixins.media('tablet-l') {
      margin: 28% auto;
    }
  }
}

.post-popup__wrapper {
  max-height: 90vh;
  @include mixins.boxFlex(initial, initial, column);

  @include mixins.media('tablet') {
    max-height: initial;
  }

  @include mixins.media('tablet-l') {
    flex-direction: row;
  }
}

.post-popup__image {
  position: relative;
  padding-bottom: 100%;
  width: 100%;

  @include mixins.media('tablet-l') {
    padding-bottom: 62.2%;
  }

  img,
  video {
    position: absolute;
    height: 100%;
    width: 100%;
    object-fit: cover;
  }
}

.post-popup__content {
  position: relative;
  padding: 20px 20px 0 20px;
  max-width: 377px;
  max-height: 345px;
  overflow-y: auto;
  font-size: variables.$font-size--general-m;

  @include mixins.media('tablet-l') {
    max-height: 622px;
  }

  &::-webkit-scrollbar {
    display: none;
  }

  hr {
    margin: 0;
  }
}

.post-popup__header {
  padding-bottom: 15px;
  @include mixins.boxFlex;

  img {
    height: 52px;
    width: 52px;
    margin-right: 10px;
    border-radius: 100%;
  }
}

.post-popup__profile {
  a {
    color: variables.$color-black;
    display: block;
  }

  span {
    display: block;
  }

  span:nth-child(1) {
    margin-bottom: 4px;
    font-weight: 600;
    font-size: variables.$font-size--general-m;
    color: variables.$color-black;
  }

  span:nth-child(2) {
    font-weight: 400;
    font-size: variables.$font-size--general-s;
    color: variables.$color-background-2;
  }
}

.post-popup__controls {
  padding-top: 20px;
  @include mixins.boxFlex(space-between);

  button {
    line-height: 0;
    @include mixins.clickEffect;

    &[disabled=true] {
      opacity: .3;
      cursor: initial;
      pointer-events: none;
    }
  }

  svg {
    display: block;
  }

  button.prev svg {
    transform: rotate(90deg);
  }

  button.next svg {
    transform: rotate(-90deg);
  }
}

.post-popup__likes {
  padding: 20px 0;
}

.post-popup__caption {
  padding-bottom: 40px;
}

.post-popup__caption-description {
  line-height: variables.$line-height--general-m;
  white-space: pre-line;
}

.post-popup__footer-wrapper {
  position: sticky;
  bottom: 0;
  padding: 10px 0 20px;
  width: 100%;
  background-color: variables.$color-white;

  @include mixins.media('tablet-l') {
    padding: 20px 0;
  }
}

.post-popup__footer {
  @include mixins.boxFlex(initial);

  .post-popup__date {
    margin-right: 5px;
    font-size: variables.$font-size--general-s;
    text-transform: uppercase;
    @include mixins.boxFlex(initial, center);

    &::after {
      display: block;
      content: 'Â·';
      font-size: 20px;
      margin-left: 5px;
    }
  }

  .post-popup__link {
    font-size: variables.$font-size--general-s;
    color: variables.$color-black;
    font-weight: 500;
  }
}

.post-popup__close {
  position: relative;
  z-index: 50;
  margin-bottom: auto;
  margin-left: auto;
  @include mixins.clickEffect;

  svg {
    display: block;
    height: 13px;
    width: 13px;
  }
}
